@import "../../css-config/mixins.scss";

.category-main-content {
    margin: 30px 0;

    @include md {
        margin: 75px 0;
    }

    .layout {
        max-width: calc(100% - 20px);
        margin: 0 auto;

        @include md {
            max-width: 1200px;
        }
    }

    .category-title {
        font-size: 24px;

        @include md {
            font-size: 34px;
        }

        &:after {
            content: "";
            display: block;
            margin-top: 5px;
            width: 140px;
            height: 2px;
            background-color: #ad07e9;

            @include md {
                margin-top: 10px;
            }
        }
    }
}