@import "../../../css-config/mixins.scss";

.banner {
    padding: 40px 0;
    position: relative;

    @include md {
        height: calc(100vh - 80px);
    }

    .content {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
        margin: 0 auto;

        @include md {
            flex-direction: row;
            max-width: 1200px;
        }

        .banner-img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 0;

        }

        .text-content {
            color: white;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            z-index: 1;

            @include md {
                position: absolute;
                left: 50px;
                top: 50%;
                padding-left: 302px; //extra
                transform: translateY(-50%);
            }

            @include xxl {
                left: 0;
            }

            h1 {
                font-weight: 800;
                font-size: 40px;
                line-height: 1;
                margin-bottom: 20px;


                @include md {
                    font-size: 145px;
                }
            }

            p {
                max-width: 300px;
                font-size: 14px;
                line-height: 20px;
                margin-bottom: 20px;

                @include md {
                    max-width: 500px;
                    font-size: 18px;
                    line-height: 24px;
                    margin-bottom: 40px;
                }
            }

            .ctas {
                display: flex;
                justify-content: center;
                gap: 20px;

                .bannar-cta {
                    text-transform: uppercase;
                    font-size: 13px;
                    font-weight: 500;
                    // width: fit-content;
                    border: 2px solid white;
                    padding: 10px 20px;
                    cursor: pointer;

                    &.v2 {
                        background-color: white;
                        color: black;
                    }

                    &.hover {
                        opacity: 0.6;
                    }
                }
            }
        }

    }
}